.attendance-detail-container {
  padding: 20px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.09);
  margin-top: 20px;
  margin-bottom: 30px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.attendance-detail-title {
  margin-bottom: 10px;
  color: #222;
  font-size: 1.5rem;
  font-weight: bold;
  text-align: right;
}

.attendance-table-wrapper {
  overflow-x: auto;
}

.attendance-table {
  width: 100%;
  border-collapse: collapse;
  background: #fafafa;
  border-radius: 10px;
  overflow: hidden;
}

.attendance-table th,
.attendance-table td {
  padding: 14px 8px;
  text-align: center;
  border-bottom: 1px solid #eee;
  font-size: 1rem;
}

.attendance-table th {
  background: #f7e8b3;
  font-weight: 700;
  color: #333;
}

.status-dot {
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  margin-left: 8px;
  margin-right: 2px;
  vertical-align: middle;
  box-shadow: 0 1px 4px rgba(0,0,0,0.07);
}
.status-dot.present {
  background: #4CAF50;
}
.status-dot.absent {
  background: #f44336;
}
.status-dot.late {
  background: #ff9800;
}

.status-label {
  font-weight: 500;
  font-size: 1.05em;
  vertical-align: middle;
}

@media (max-width: 600px) {
  .attendance-detail-container {
    padding: 8px;
    border-radius: 6px;
    margin-top: 10px;
  }
  .attendance-table th,
  .attendance-table td {
    padding: 7px 2px;
    font-size: 0.95em;
  }
  .attendance-detail-title {
    font-size: 1.1em;
  }
}


.date-header {
  margin-bottom: 20px;
  text-align: center;
  padding: 15px;
  background: linear-gradient(135deg, #4CAF50, #45a049);
  border-radius: 5px;
  color: white;
}

.date-header p {
  margin: 0;
  font-size: 20px;
  font-weight: bold;
}

.attendance-table {
  width: 100%;
}

.attendance-table table {
  width: 100%;
  border-collapse: collapse;
}

.attendance-table th,
.attendance-table td {
  padding: 12px;
  text-align: center;
  border-bottom: 1px solid #eee;
}

.attendance-table th {
  background: #f5f5f5;
  font-weight: 600;
}

.status-dot {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin: 0 5px;
  cursor: pointer;
  transition: transform 0.2s;
}

.status-dot.present {
  background: #4CAF50;
}

.status-dot.absent {
  background: #f44336;
}

.status-dot.late {
  background: #ff9800;
}

.status-dot:hover {
  transform: scale(1.2);
}
